@use 'base/variables' as v;

.panel-admin{
  display: grid;
  grid-template-columns: 20% 80%;

  grid-template-areas: 
  "header header"
  "navegacion panel";


  h2{
    color: v.$titleColor;
  }

  .nro-habitacion,
  .rut{
    padding-left: calc(v.$separacion / 2);
    text-transform: uppercase;
  }
}