  box-sizing: border-box;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  color: #333333;
  margin: 0;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

a {
  color: #509EE3;
  text-decoration: none;
  transition: all 0.5s ease-out;
}

img {
  max-width: 100%;
  height: auto;
}

/* Utilidades */
.box-shadow-1 {
  box-shadow: 0.25rem 0.25rem 1rem rgba(0, 0, 0, 0.25);
}

.section {
  background-color: #F3F3F3;
  padding: 2rem 1.5rem;
  grid-column: 2/3;
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 120rem;
}

.none {
  display: none;
}

.text-top {
  align-self: flex-start;
  margin-top: 1.25rem;
}

.deshabilitado {
  pointer-events: none;
  opacity: 0.5;
}

.p-t-0 {
  padding-top: 0;
}

.m-b-10 {
  margin-bottom: 1rem;
}

.expand-2 {
  grid-column: span 2;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.disponible {
  background-color: #329f00;
}

.ocupada {
  background-color: #cb0000;
}

.reservada {
  background-color: #2020d9;
}

.mantenimiento {
  background-color: #b8a11e;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  font-size: 2rem;
  line-height: 1.6;
  margin: 0;
}

.nombre-pagina {
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
  margin-bottom: 5rem;
}

.contenido-app {
  background-color: #14192D;
}
@media (min-width: 768px) {
  .contenido-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenido-app .imagen {
  height: 20rem;
  background-image: url("../img/background-image.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 768px) {
  .contenido-app .imagen {
    height: auto;
  }
}
.contenido-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
.contenido-app .app h1,
.contenido-app .app p,
.contenido-app .app label {
  color: #FFFFFF;
}

.barra {
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.panel-admin {
  display: grid;
  grid-template-columns: 20% 80%;
  grid-template-areas: "header header" "navegacion panel";
}
.panel-admin h2 {
  color: #222222;
}
.panel-admin .nro-habitacion,
.panel-admin .rut {
  padding-left: 2.5rem;
  text-transform: uppercase;
}

.habitaciones {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(4, 1fr);
}

.habitacion {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 1rem;
  justify-items: center;
  align-items: center;
  color: #FFFFFF;
  padding-top: 1.5rem;
}
.habitacion:hover {
  opacity: 0.8;
}
.habitacion .icono {
  width: 6rem;
}
.habitacion .numero {
  font-size: 2.5rem;
  font-weight: 700;
}
.habitacion .precio {
  font-weight: 300;
}
.habitacion .estado {
  text-transform: uppercase;
  padding: 0.5rem 0;
}
.habitacion .precio,
.habitacion .estado,
.habitacion hr {
  width: 100%;
  grid-column: span 2;
}

.disponible .estado {
  background-color: #226c00;
}

.ocupada .estado {
  background-color: #980000;
}

.reservada .estado {
  background-color: #1919ad;
}

.mantenimiento .estado {
  background-color: #8c7b17;
}

.seccion {
  display: none;
  padding: 5rem 0;
}
.seccion h2 {
  color: #FFFFFF;
}
.seccion.mostrar {
  display: block;
}
.seccion.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 1rem;
  text-align: center;
  transition-property: transform;
  transition-duration: 300ms;
  cursor: pointer;
}
.listado-servicios .servicio.seleccionado {
  background-color: #008080;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}
.listado-servicios .servicio:hover {
  transform: scale(1.1);
}
.listado-servicios .servicio p {
  color: #000000;
  margin: 0 0 1.25rem;
}
.listado-servicios .servicio .nombre-servicio,
.listado-servicios .servicio .precio-servicio,
.listado-servicios .servicio .cama-servicio {
  font-weight: 700;
}
.listado-servicios .servicio .nombre-servicio {
  color: #14192D;
  font-size: 2rem;
}
.listado-servicios .servicio .precio-servicio {
  color: #008080;
  font-size: 3rem;
  margin: 0;
}
.listado-servicios .servicio .cama-servicio {
  color: #CD5C5C;
}

.contenido-resumen h2,
.contenido-resumen h3 {
  color: #FFFFFF;
}
.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #509EE3;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedor-servicio {
  padding: 3rem 0;
  border-bottom: 1px solid #CCCCCC;
}
.contenedor-servicio:last-of-type {
  margin-bottom: 5rem;
}

.titulo-dashboard {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 0.5rem;
}
.titulo-dashboard .text-left {
  margin: 0;
}
.titulo-dashboard p {
  color: #666666;
}

.estadisticas {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  grid-template-areas: "grafico-ventas grafico-ventas grafico-ventas" ". . .";
  max-width: 100%;
}
.estadisticas .grafico-ventas {
  grid-area: grafico-ventas;
  width: 60%;
}

.fomulario {
  width: 100%;
}

.campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.campo label,
.campo input {
  flex: 0 0 12rem;
  font-size: 2rem;
}
.campo input,
.campo select,
.campo textarea {
  flex: 1;
  padding: 1.25rem;
  border-radius: 1rem;
  border: none;
  outline: none;
  resize: none;
  font-size: 2rem;
}

.boton {
  background-color: #008080;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 2rem 0 5rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: rgba(0, 128, 128, 0.5019607843);
  cursor: pointer;
}

.boton-logout {
  background-color: #000000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 2rem 0 5rem;
  margin: 0;
}
@media (min-width: 768px) {
  .boton-logout {
    width: auto;
  }
}
.boton-logout:hover {
  background-color: rgba(0, 0, 0, 0.3333333333);
  cursor: pointer;
}

/* Componentes boton animacion icono desplazado */
.boton-crear {
  background-color: #008080;
  color: #FFFFFF;
  padding: 1rem 0;
  font-size: 1.7rem;
  border: none;
  border-radius: 0.7em;
  letter-spacing: 0.08em;
  position: relative;
  padding-left: 2.8em;
  padding-right: 0.9em;
  width: 65%;
}
.boton-crear .icon {
  background-color: #14192D;
  height: 2em;
  width: 2em;
  border-radius: 2em;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 0.4em;
  padding: 0.5rem;
  transition: all 0.5s;
  cursor: pointer;
}
.boton-crear path {
  margin-left: 0.4em;
  transition: all 0.5s;
  width: 1.2rem;
  height: 1.2rem;
  fill: #FFFFFF;
}
.boton-crear p {
  font-size: 2rem;
}
.boton-crear:hover .icon svg {
  transform: rotate(360deg);
}
.boton-crear:hover .icon {
  width: calc(100% - 0.9em);
  border-radius: 0.5em;
}

.boton-actualizar {
  background-color: #14192D;
  color: #FFFFFF;
  padding: 1rem 0;
  font-size: 1.7rem;
  border: none;
  border-radius: 0.7em;
  letter-spacing: 0.08em;
  position: relative;
  padding-left: 2.8em;
  padding-right: 0.9em;
  width: 65%;
}
.boton-actualizar .icon {
  background-color: #FFFFFF;
  height: 2em;
  width: 2em;
  border-radius: 2em;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 0.4em;
  padding: 0.5rem;
  transition: all 0.5s;
  cursor: pointer;
}
.boton-actualizar path {
  margin-left: 0.4em;
  transition: all 0.5s;
  width: 1.2rem;
  height: 1.2rem;
  fill: #333333;
}
.boton-actualizar p {
  font-size: 2rem;
}
.boton-actualizar:hover .icon svg {
  transform: rotate(360deg);
}
.boton-actualizar:hover .icon {
  width: calc(100% - 0.9em);
  border-radius: 0.5em;
}

/* Componente boton volver con flechita */
.boton-volver {
  display: flex;
  height: 3em;
  width: 12rem;
  margin: 2.5rem 0;
  margin-left: 1.25rem;
  padding: 1rem;
  align-items: center;
  justify-content: center;
  background-color: #CD5C5C;
  color: #FFFFFF;
  border-radius: 0.3rem;
  letter-spacing: 0.1rem;
  transition: all 0.2s linear;
  cursor: pointer;
  border: none;
}
.boton-volver > svg {
  width: 3.2rem;
  height: 3.2rem;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
  font-size: 2rem;
  fill: #FFFFFF;
  transition: all 0.4s ease-in;
}
.boton-volver:hover > svg {
  font-size: 1.2em;
  transform: translateX(-5px);
}
.boton-volver:hover {
  box-shadow: 9px 9px 33px #d1d1d1, -9px -9px 33px #ffffff;
  transform: translateY(-2px);
}

