  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #008080;
  height: 6.5rem;
  padding: 2rem;
  color: #FFFFFF;
  grid-area: header;
}

.logo h1 {
  text-transform: uppercase;
  font-weight: 300;
