  align-self: start;
}
.menu button {
  color: inherit;
  background-color: #222222;
  padding: 1.5rem 1rem;
  text-align: center;
  font-weight: 700;
  width: 100%;
  border: none;
  outline: none;
  transition: all 0.5s ease-in-out;
}
.menu button:hover {
  cursor: pointer;
  background-color: #090909;
  color: white;
}

.navegacion details {
  padding: 1rem 2rem;
}
.navegacion details.active {
  padding: 0;
  margin: 1rem 0;
}
.navegacion details.active summary {
  padding-left: 0;
  justify-content: center;
  transition: all 0.7s ease-in-out;
}
.navegacion summary {
  display: flex;
  align-items: center;
  gap: 2.5rem;
  padding-left: 2rem;
}
.navegacion summary:hover {
  opacity: 0.7;
  cursor: pointer;
}

.opciones {
  display: flex;
  flex-direction: column;
}
.opciones.active a {
  flex-direction: column;
  gap: 0.3rem;
}
.opciones a {
  display: inherit;
  align-items: center;
  gap: 2.5rem;
  color: #FFFFFF;
  padding: 0.5rem;
  padding-left: 2rem;
  margin-left: 1rem;
  transition: color 0.5s ease-out;
}
.opciones a:hover {
  color: #509EE3;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  opacity: 1;
  visibility: visible;
  transition: all 1s ease-out;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}
.alerta.active {
  opacity: 0;
  visibility: hidden;
}

.formulario-crud {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2.5rem;
  padding: 2.5rem;
}
.formulario-crud label {
  font-weight: 700;
  text-transform: uppercase;
  font-size: initial;
}
.formulario-crud input,
.formulario-crud select,
.formulario-crud textarea {
  border: thin solid #CCCCCC;
  color: #666666;
}
.formulario-crud input option,
.formulario-crud select option,
.formulario-crud textarea option {
  color: #000000;
}

table.tabla {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.tabla thead th {
  color: #FFFFFF;
  padding: 2rem;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 1.8rem;
}
table.tabla tbody {
  text-align: center;
}
table.tabla tbody tr,
table.tabla tbody td {
  border-bottom: thin solid #CCCCCC;
}
table.tabla tbody td {
  padding: 1rem;
  padding-right: 0;
  font-size: 1.9rem;
}
table.tabla a,
table.tabla button {
  display: block;
  width: 100%;
  margin-bottom: 1rem;
}

.tabla-clientes thead {
  background-color: rgba(205, 92, 92, 0.5019607843);
}

.tabla-huespedes thead {
  background-color: rgba(32, 32, 217, 0.5019607843);
}

.tabla-habitaciones thead {
  background-color: rgba(20, 25, 45, 0.5019607843);
}

.tabla-usuarios thead {
  background-color: rgba(0, 128, 128, 0.5019607843);
}

.tabla-ventas thead {
  background-color: rgba(184, 161, 30, 0.5019607843);
}
.tabla-ventas li {
  text-align: left;
  margin-left: 1rem;
}

.modal {
  width: 40% !important;
  padding: 2.5rem !important;
}
.modal .modal-icon {
  width: 6rem !important;
  height: 6rem !important;
}
.modal .modal-title {
  color: #14192D;
}
.modal .modal-warning {
  font-size: 1.5rem;
  color: #333333;
}

.modal-btns {
  width: 100%;
  gap: 2rem;
}
.modal-btns .modal-btn-confirm,
.modal-btns .modal-btn-cancel {
  padding: 1rem 1.5rem;
  font-size: 1.5rem !important;
  font-weight: 700;
}
.modal-btns .modal-btn-confirm {
  background-color: #008080 !important;
}
.modal-btns .modal-btn-cancel {
  background-color: #CD5C5C !important;
}

.detalle-habitacion {
  background-color: #FFFFFF;
  border-radius: 0.5rem;
  border-top: medium solid #008080;
  text-transform: uppercase;
  margin-bottom: 2.5rem;
}
.detalle-habitacion .titulo {
  padding: 1.25rem;
  border-bottom: thin solid #CCCCCC;
  font-weight: 900;
  color: #222222;
}
.detalle-habitacion .titulo,
.detalle-habitacion .datos-habitacion {
  padding: 1.25rem;
}

.datos-habitacion {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(3, 1fr);
}
.datos-habitacion li {
  color: #008080;
  font-weight: 700;
  padding: 1rem;
  list-style: none;
}
.datos-habitacion span {
  display: inline-block;
  margin-left: 0.5rem;
  color: #333333;
  font-weight: 400;
  /*  &.precio{
     background-color: v.$secondColor;
     font-weight: v.$regular;
   } */
}
.datos-habitacion span.disponible, .datos-habitacion span.reservada, .datos-habitacion span.ocupada, .datos-habitacion span.mantenimiento {
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  color: #FFFFFF;
  font-weight: 700;
  letter-spacing: 0.1rem;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #008080;
  color: #FFFFFF;
  border: none;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms ease;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #004d4d;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #008080;
}

.paginacion {
  padding: 5rem 0;
}
@media (min-width: 480px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem;
}

.ocultar {
  visibility: hidden;
}

.titulo-reportes {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 20rem;
}
.titulo-reportes .boton-reporte {
  margin: 0;
}